import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/apiCalls/api-call.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  public counter: number = 0;
  location: string
  role: any;
  sparePart: any;
  constructor(private apiCall: ApiCallService) { }

  ngOnInit() {
    this.role = this.apiCall.getRole()

  }


  increment() {
    this.counter += 1;
  }

  decrement() {
    this.counter -= 1;
  }

  findlocation(data) {

    this.apiCall.findLocation(data).subscribe((res: any) => {
      if (res.status == 'false') {
        alert(res.message)
      } else {

        this.location=res.result[0].location
        console.log(this.location)
      }
    }, (err: any) => {

        alert(err.message)

    })
  }

  createSpareItem(data) {
    console.log(data)
    this.apiCall.create(data).subscribe((res: any) => {
      if (res.status == 'sucess') {
        alert(res.message)
      }
    })
  }


  getSpareParts(data){
    this.apiCall.getSpareParts(data).subscribe((res: any) => {
      console.log(res)
      if (res.status == 'false') {
        alert(res.message)
      } else {
        this.sparePart=res

      }
    }, (err: any) => {

        alert(err.message)

    })

  }

  buyProduct(){
    alert(this.counter+" "+'SpareParts purchased sucessfully')
  }


  createBranch(data){
    this.apiCall.createBranch(data).subscribe((res:any)=>{
      console.log(res);
      alert(res.message)
    })
  }
}
